.spotiCard a {
    text-decoration: none !important;
    color: black;
}

.spotiCard {
    transition: 0.5s;
}

.spotiCard:hover {
    transform: scale(1.0125);
    box-shadow: 0px 0px 30px -6px rgba(49, 235, 22, 1);
}

@media (max-width:500px) {
    .spotiCard {
        width: 80%;
        margin: 0 auto;
    }
    .spotiCard img {
        height: 250px;
    }
}