.img-thumb {
    width: 70px;
    height: 70px;
}

.img-circle {
    border-radius: 100%;
    width: 200px;
}

.widget {
    width: 100%;
    height: 80px;
    border: 0;
}

@media (max-width:500px) {
    .resp {
        display: none;
    }
    .img-circle {
        position: fixed;
        width: 90%;
        height: 230px;
        opacity: 0.1;
        border-radius: 10px;
        transform: translateY(-30px);
    }
    table {
        margin-left: 15px;
    }
}